class LanguageSkill extends React.Component{
    constructor(props) {
        super(props);
        this.style = {
            width: this.props.percent.toString(),
        }
    }
    render() {
        return(
            <div className="col-md-3">
                <h5>{this.props.lang}</h5>
                <div className="progress beautiful progress-lg">
                    <div className={"progress-bar " + this.props.color} aria-valuenow="80" aria-valuemin="0"
                         aria-valuemax="100" style={this.style}>{this.percent}
                    </div>
                </div>
            </div>
        );
    }
}

